body{
  background-color: #fff !important;
}

.App {
  text-align: center;
  
}
.service-card{
  cursor: pointer;
  transition: 0.5s all;
}
.service-card:hover{
  background-color: #108BEA;
  color: #fff;
}


.social-media-customize{
    height: 22px ;
    margin-top: 9px;
    margin-right: 7px;
    cursor: pointer;
}
.social-media-customize:hover{
   -webkit-transform: scale(1.05);
           transform: scale(1.05);
   
 
}
.custom-footer-link{
    cursor: pointer;
    
}
.custom-footer-link:hover{
    -webkit-transform: scale(1.02);
            transform: scale(1.02);
   
}
.quick-link{
    text-decoration: none;
    color:white;
}
.quick-link:hover{
    -webkit-transform: scale(1.02);
            transform: scale(1.02);
   
}
.header-name img{
    width: 200px;
    height: 50px;
    
}
.header-nav a{
    color: black;
    transition: 0.5s all ease-in-out;
}
.logout-btn{
    border: none;
    background-color: transparent;
    border:  1px solid #168EEA;
    border-radius: 5px;
    padding: 2px 5px;
}










.about-img{
    background-image: url('https://images.pexels.com/photos/5213883/pexels-photo-5213883.jpeg?auto=compress&cs=tinysrgb&dpr=1&w=500');

    height: 70vh;
    width: 100%;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
    /* margin-top:20px; */
    margin-bottom:30px;
  
}
.about-para{
    margin-top: 100px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
}
.about-para h3{
    font-size: 40px;
    font-weight: 500;
}
.about-para p{
    padding-left: 100px;
    padding-right: 100px;
    font-size: 18px;
    
}
.heading{
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
    padding: 10px 0;
   margin-bottom: 20px;
    
}
.user-order-detail:nth-child(even){
background-color: rgb(211, 211, 211);   
    
}
.user-order-detail{
    box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
    padding: 10px 0;
   margin-bottom: 20px;
    
}
.user-order-detail:hover{
    box-shadow: rgba(6, 24, 44, 0.4) 0px 0px 0px 2px, rgba(6, 24, 44, 0.65) 0px 4px 6px -1px, rgba(255, 255, 255, 0.08) 0px 1px 0px inset;
    
}
.dashboard-bg {
    background-color: #313A46;
    height: 100vh;
}

.dashboard-contain-bg {
    background-color: #F2F2F2;

    height: 100vh;
}

.custom-dashboard-link a {
    color: rgb(255, 255, 255);
    text-decoration: none;
    padding-left: 30px;
    padding-top: 5px;
    text-align: left;
}

.custom-dashboard-link a:hover {
    color: #0DCAF0;
}

.dashboard-header {
    
    padding-left: 50px;
    
    background-color: white;
    box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;

}
